import Joi from 'joi';

export const secondFormValidator = Joi.object({
    name: Joi.string().pattern(/^[a-zA-Z-- ]{1,100}$/).min(2).max(40).required().messages({
        'string.pattern.base': '*  \'',
        'string.min': '*min 2 ',
        'string.max': '*max 40 ',
        'string.empty': '*     ',
    }),
    phoneNumber: Joi.string().pattern(/^(\+?(38|48|39|34|49|1))?0\d{9}$/).required().messages({
        'string.pattern.base': '*  ',
        'string.empty': '*     ',
    }),
    date: Joi.string().required()
})